<template>
  <nav class="navbar navbar-default">
    <div class="container-fluid">
      <div class="navbar-header">
        <router-link to="/" class="navbar-brand">Stock Trader</router-link>
      </div>
      <div class="collapse navbar-collapse">
        <ul class="nav navbar-nav">
          <router-link to="/portfolio" activeClass="active" tag="li"><a href="#">Portfolio</a></router-link>
          <router-link to="/stocks" activeClass="active" tag="li"><a href="#">Stocks</a></router-link>
        </ul>
        <strong class="navbar-text navbar-right">Funds: {{ funds | currency }}</strong>
        <ul class="nav navbar-nav navbar-right">
          <li><a href="#" @click.prevent="endDay">End Day</a></li>
          <li class="dropdown" :class="{open: isDropdownOpened}" >
            <a href="#" class="dropdown-toggle" @click.prevent="isDropdownOpened = !isDropdownOpened">Save &amp; Load <span class="caret"></span></a>
            <ul class="dropdown-menu">
              <li><a href="#" @click.prevent="saveData">Save Data</a></li>
              <li><a href="#" @click.prevent="loadData">Load Data</a></li>
            </ul>
          </li>
        </ul>
      </div>
    </div>
  </nav>
</template>

<script>
  import {mapActions} from 'vuex';

  export default {
    data() {
      return {
        isDropdownOpened: false
      }
    },
    computed: {
      funds() {
        return this.$store.getters.funds;
      }
    },
    methods: {
      ...mapActions({
        randomizeStocks: 'randomizeStocks',
        fetchData: 'loadData'
      }),
      endDay() {
        this.randomizeStocks();
      },
      saveData() {
        const data = {
          funds: this.$store.getters.funds,
          stockPortfolio: this.$store.getters.stockPortfolio,
          stocks: this.$store.getters.stocks
        };

        this.$http.put('data.json', data);
        this.isDropdownOpened = false;
      },
      loadData() {
        this.fetchData();
        this.isDropdownOpened = false;
      }
    }
  }
</script>


